body {
  font-family: sans-serif;
  font-size: "10em";
  background-color: rgba(0, 0, 0, 1);
}

h1 {
  color: white;
}


p {
  color: white;
}

label {
  color: white;
}

.main-container {
  padding: 30px;
  text-align: center;
}

.button-panel-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}


.button-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  border-radius: 2px;
  border: 2px solid rgb(20, 20, 20);
  background-color: rgb(10, 10, 10);
}


button {
  width: 150px;
  color: white;
  font-weight: bold;
  padding: 1em;
  margin: 2px;
  cursor:pointer;
}


.sort-button {
  background-color: rgb(120, 0, 110);
  border: 1px solid rgb(120, 0, 110);
}

.sort-button:hover {
  background-color: rgb(230, 230, 230);
  border: 1px solid white;
  color: rgb(120, 0, 110);
}

.randomize-button {
  background-color: rgb(0, 0, 140);
  border: 1px solid rgb(0, 0, 140);
}

.randomize-button:hover {
  background-color: rgb(230, 230, 230);
  border: 1px solid white;
  color: rgb(0, 0, 140);
}
